/**
 * 
 * @param {string} action 
 * @param {string} label ID
 * @param {object=} opt 
 * @param {string=} opt.category'general'
 * @param {number=} opt.value
 * @param {boolean=} opt.g: true
 * @param {boolean=} opt.b: true
 */
let statisticsAll = function(action, label, opt = null) {
    let option = {
        category: 'general',
        value: null,
        g: true,
        b: true,
    };

    /**
     * 
     * _hmt.push(['_trackEvent', category, action, opt_label, opt_value]);
     * category
     * action
     * opt_label
     * opt_value
     */
    let hmtPush = function(category, action, label, value) {
        window._hmt.push(['_trackEvent', category, action, label, value]);
    };

    /**
     * 
     */
    let gaStatistics = function(category, action, label, value) {
        window.gtag('event', action, {
            event_category: category,
            event_label: label,
            value: value,
        });
    };

    Object.assign(option, opt);

    if (option.b) {
        hmtPush(option.category, action, label, option.value);
    }

    if (option.g) {
        gaStatistics(option.category, action, label, option.value);
    }
};

export default statisticsAll;
