function getWindowSize() {
    /**
     * 
     * window.innerWidth window.innerHeight
     * document.body.clientWidth document.body.clientHeight
     * document.documentElement.clientWidth document.documentElement.clientHeight
     *  document.body.clientWidth > document.documentElement.clientWidth > window.innerWidth
     * window.innerWidthdocument.documentElement<html>document.body<body>
     * <body>bodybodyhtml
     */

    let width;
    let height;

    let wBody, hBody;
    let wDocument, hDocument;
    let wWindow, hWindow;

    if (window.body) {
        wBody = document.body.clientWidth;
        hBody = document.body.clientHeight;
    }

    if (window.documentElement) {
        wDocument = document.documentElement.clientWidth;
        hDocument = document.documentElement.clientHeight;
    }

    wWindow = window.innerWidth;
    hWindow = window.innerHeight;

    width = wBody || wDocument;
    height = hBody || hDocument;

    if (!width && !height) {
        width = wWindow;
        height = hWindow;
    }

    return {
        width,
        height,
    };
}


function makeRem750() {
    // 7.5rem6.4rem750rpx7.5rem6.4rem750px640px
    //  640px = 6.4rem
    //    750px = 7.5rem
    // 7.5rem750px
    // chrome13pxrem640px,640remfont-size*100
    // pxvw update by 3c 2019-11-04 1056

    let unit = 100 / 7.5;
    document.documentElement.style.fontSize = unit + 'vw';
    return unit;
}

