import axios from 'axios';
import wx from 'jweixin-npm';
const URL =
    'http://tianmao2020.herdsric.com/share/getJsSignature.go';

const WX_SHARE_DATA = {
    title: '11',
    content: ' ',
    img: 'http://ifscdn.herdsric.com/tianmao2020/share.png',
    link: window.location.href,
};


const wxShare = () => {
    axios
        .get(URL, { params: { url: window.location.href } })
        .then(function(res) {
            const data = res.data.data;
            wx.config({
                debug: false, // ,apialertpclogpc
                appId: data.appId, // 
                timestamp: data.timestamp, // 
                nonceStr: data.noncestr, // 
                signature: data.signature, // 1
                jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage'],
            });
            wx.ready(function() {
                // wx.checkJsApi({
                //     jsApiList: ['showMenuItems'],
                //     success: function(res) {
                //         wx.showMenuItems({
                //             menuList: [
                //                 'menuItem:share:appMessage', //
                //                 'menuItem:share:timeline', //
                //             ],
                //         });
                //     },
                // });
                //
                wx.onMenuShareTimeline({
                    title: WX_SHARE_DATA.title, // 
                    desc: WX_SHARE_DATA.content, //
                    link: WX_SHARE_DATA.link, // 
                    imgUrl: WX_SHARE_DATA.img, // 
                });
                //
                wx.onMenuShareAppMessage({
                    title: WX_SHARE_DATA.title, // 
                    desc: WX_SHARE_DATA.content, //
                    link: WX_SHARE_DATA.link, // 
                    imgUrl: WX_SHARE_DATA.img, // 
                });
            });
        });
}

export {
    wxShare
}