// 
let navController = {
    isIE: false,
    isAndroid: false,
    isIos: false,
    isWX: false,
    isDD: false,
    isKB: false,
    isWB: false,
    isQQSpace: false,
    isWebApp: false,
    isMac: false,
    isEdge: false,
    isQQBrowser: false,
    isMobile: false,
    isIpad: false,
    isPC: false,
    init() {
        let u = navigator.userAgent;
        let v = {
            //
            trident: u.indexOf('Trident') > -1, //IE
            presto: u.indexOf('Presto') > -1, //opera
            webKit: u.indexOf('AppleWebKit') > -1, //
            gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1, //
            mobile: !!u.match(/AppleWebKit.*Mobile.*/), //
            ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios
            android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1, //androiduc
            iPhone: u.indexOf('iPhone') > -1, //iPhoneQQHD
            iPad: u.indexOf('iPad') > -1, //iPad
            webApp: u.indexOf('Safari') == -1, //web
            mac: u.indexOf('Mac') > -1,
            edge: u.indexOf('Edge') > -1,
            qqBrowser: u.indexOf('QQBrowser') > -1,
        };
        // let language = (navigator.browserLanguage || navigator.language).toLowerCase();
        // IE
        this.isIE = v.trident;
        this.isEdge = v.edge;
        this.isMac = v.mac;
        this.isQQBrowser = v.qqBrowser;
        this.isMobile = v.mobile;
        this.isIpad = v.iPad;
        this.isIos = v.ios;
        this.isAndroid = v.android;
        this.isWebApp = v.webApp;
        // this.isPC = ((!this.isMobile && !this.isIpad) && true) || false;

        if (v.mobile) {
            // 
            let ua = u.toLowerCase(); //

            if (ua.match(/MicroMessenger/i) == 'micromessenger') {
                //
                this.isWX = true;
            }
            if (ua.match(/WeiBo/i) == 'weibo') {
                //
                this.isWB = true;
            }
            if (ua.match(/QQ/i) == 'qq') {
                //QQ
                this.isQQSpace = true;
            }
            if (ua.match(/koubeidefined/i) == 'koubeidefined') {
                //
                this.isKB = true;
            }
            if (ua.match(/dingtalk/i) == 'dingtalk') {
                //
                this.isDD = true;
            }
        }
    },
    output() {
        console.log('navi controller:', navigator.userAgent);
        console.log('navi filter:', navController);
    },
};

navController.init();

export default navController;